import React from 'react'
import { NavLink, Route } from 'react-router-dom'; 
import './StudyMeetingContainer.scss'

function StudyMeetingContainer({ match }) {
    return (
        <section>
            <p className="study-title"> </p>
            <article className="study-article">
                <NavLink exact to="/study/post"><p>React  </p></NavLink>
                <p>django  </p>
                <NavLink exact to="/study/write"><button className="study-write"> </button></NavLink>
                <div className="search-box">
                    <input type="text" className="search-input"/>
                    <button className="search-btn"></button>
                </div>
            </article>
        </section>
    )
}

export default StudyMeetingContainer
